}

.hover_effect {
  display: block;
  width: 100%;
  position: relative;
  height: 20px;
}

.hover_effect:after {
  content: '';
  position: absolute;
  display: block;
  top: 50%;
  right: -10px;
  left: -10px;
  border-bottom: 1px solid white;
  opacity: 0.5;
  transform: scaleX(0);
  transform-origin: 100% 0;
  transition: 0.2s transform;
}

.hover_effect:hover:after {
  transform: scaleX(1);
  transform-origin: 0 0;
}

.hover__effect_r {
  position: absolute;
  top: -50%;
  overflow: hidden;
  transition: 0.6s transform;
  display: block;
  height: 20px;
  width: 100%;
}

.hover__effect_l {
  position: absolute;
  top: 50%;
  overflow: hidden;
  transition: 0.6s transform;
  display: block;
  height: 20px;
  width: 100%;
}

.hover__effect_r span {
  display: inline-block;
  height: 100%;
  transform: translateY(63%);
}

.hover__effect_l span {
  display: inline-block;
  height: 100%;
  transform: translateY(-37%);
}

.hover__effect_shade {
  opacity: 0;
}

.hover_effect:hover .hover__effect_l {
  transform: translateX(2px);
}
.hover_effect:hover .hover__effect_r {
  transform: translateX(-2px);
}

